import React from 'react';
import {Switch,Route} from 'react-router-dom';
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import Navbar from './components/Navbar';
import ProductList  from './components/ProductList';
import Details from './components/Details';
import Cart from './components/Cart';
import Default from './components/Default';
import Contact from './components/Contact';
import Modal from './components/Modal';
function App() {
  return (
    <React.Fragment>
      <Navbar />
      <Switch>
        <Route exact path="/react-ecommerce-store/" component={ProductList} />
        <Route path="/react-ecommerce-store/details" component={Details} />
        <Route path="/react-ecommerce-store/cart" component={Cart} />
        <Route path="/react-ecommerce-store/contact" component={Contact} />
        <Route component={Default} />
      </Switch>
      <Modal />
    </React.Fragment>
  );
}

export default App;
